(function() {
  'use strict';

  angular
    .module('mediaWall')
    .controller('WallVerifiedController', WallVerifiedController);

  /** @ngInject */
  function WallVerifiedController($scope, $rootScope, $state, $timeout, $interval, $log, $sce, $sails, WallService, $window, localStorageService) {
    var vm = this;

    var updateCachedMedia = function (updatedCachedMedia) {
      localStorageService.set('cachedMedia', updatedCachedMedia);
    }
    var cachedMedia = localStorageService.get('cachedMedia');
    if (!cachedMedia) {
      cachedMedia = [];
      updateCachedMedia(cachedMedia);
    }

    // UI flags for show/hide
    vm.flags = {};
    vm.flags.enableMainFileAnimation = true;

    vm.wallMode = 'verified';

    vm.slideshow = true;

    vm.files = [];
    if (cachedMedia.length) {
      vm.files = angular.copy(cachedMedia);
    }
    vm.mainFile = null;

    // Current authenticated wall
    var wall = WallService.current();

    if (!wall) {
      $state.go('home');
    }

    var wallPassword = wall.password;

    var id = null;
    if (wall && wall.id) {
      id = wall.id;
    }

    if (!id) {
      return;
    }

    if (wall.image) {
      vm.image = wall.image;
    }

    var roomName = 'wall' + id;


    function setNewMedia(data) {
      isRandomAllowed = false;

      vm.isActiveMainImage = true;

      // New image is shown for minimal 15 seconds
      $timeout(function() {
        isRandomAllowed = true;
      }, 25000);

      var file = {
        url: data.url,
        type: data.type,
        orientation: data.orientation,
        trustedUrl: $sce.trustAsResourceUrl(data.url),
        controls: data.controls
      };

      $timeout(function () {
        $scope.$apply(function () {
          vm.files.unshift(file);
          vm.mainFile = file;

          // Main photo + 24 at mosaic
          vm.files = vm.files.slice(0, 25);

          // Update UI on new data
          updateUI();


          $timeout(function() {
            vm.isActiveMainImage = false;
          }, 18000);

          if (vm.files[0]) {
            vm.files[0].controls = false;
          }
        });
      });
    }

    // Crosstab suff --------------------------
    //
    // Listen for changes and update $scope
    crosstab.on('wallNewMedia', function(message) {
      $log.info("crosstab", message);

      setNewMedia(message.data);
    });
    // END - Crosstab suff --------------------------

    $log.info('is crosstab supported', crosstab.supported);

    // Crosstab fallback Sockets
    if (!crosstab.supported) {
      var isConnected = false;
      // Subscribe to Sails server for listen for changes
      $sails.on("connect", function () {
        $log.info('Connected to Sails sockets');
        $sails.post("/notify/subscribe", { roomName: roomName })
          .then(function (resp) {
            $log.info('Subscribe OK', resp)
            isConnected = true;
          }, function (resp) {
            $log.error('Subscribe NOT OK', resp)
          });
      });

      // TODO(stepanic): refactor this code and remove redundant code
      $timeout(function () {
        if (!isConnected) {
          $log.info('Not connected, try again!');
          $sails.post("/notify/subscribe", { roomName: roomName })
            .then(function (resp) {
              $log.info('Subscribe OK when connected', resp)
              isConnected = true;
            }, function (resp) {
              $log.error('Subscribe NOT OK when connected', resp)
            });
        }
      }, 2000);

      // Watching for updates
      $sails.on("notify-remote-approve", function (approvedFile) {
        $log.info('notify-remote-approve', approvedFile);

        setNewMedia(approvedFile);
      });
    }
    // END - Sails.js SOCKET suff --------------------------

    var isRandomAllowed = false;
    function showRandomMedia() {
      if (isRandomAllowed) {
        // var key = Math.round(Math.random() * (vm.files.length - 1));
        // console.log('randomKey', key);
        // vm.changeMainFile(key);

        // REFACTORED(stepanic): use queue instead of random
        // to more change sidebar
        var first = vm.files[0];
        vm.files.shift();
        // Add to end of array
        if (first) {
          vm.files.push(first);
        }
      }
    }

    // Random Media every 15 seconds
    $interval(showRandomMedia, 15000);

    function updateUI() {
      $scope.windowInnerHeight = $window.innerHeight;
      $scope.windowInnerWidth = $window.innerWidth;
      $scope.firstImageWidth = $scope.windowInnerWidth;

      $timeout(function () {
        // Return to 80% of window width after 15 seconds
        $scope.firstImageWidth = $scope.windowInnerWidth * 0.8;
      }, 1500);
    }

    // Update on load
    updateUI();

    // updateUI on window resize
    var w = angular.element($window);
    w.bind('resize', function () {
      $scope.$apply(function () {
        updateUI();
      });
    });

    vm.changeMainFile = function (key) {
      var old = vm.files[0];
      if (old) {
        old.controls = false;
      }
      vm.files[0] = vm.files[key];
      if (vm.files[0]) {
        vm.files[0].controls = true;
      }
      if (old) {
        vm.files[key] = old;
      }
      // console.log(old, vm.files, vm.files[key]);
    }

    // window.bla = vm.files;
    // window.bla2 = vm.changeMainFile;

    vm.ToggleWallMode = function() {
      // $state.go('wallDirect', { wallPassword: wallPassword });
      $state.go('wall');
    }

    vm.GoToRecord = function() {
      $state.go('record');
    }

    vm.isVisibleWallVerifyOption = true;
    vm.GoToWallVerify = function() {
      var url = $state.href('wallVerify', {}, {absolute:true});
      $window.open(url, '_blank');
    }


    /**
     * Remove media from UI
     */
    vm.removeFile = function (file) {
      console.log('remove', file);

      var index = vm.files.indexOf(file);

      // Remove from list
      if (index === 0) {
        if (vm.files && vm.files.length > 1) {
          // HACK to remove from first position and to release from CSS animation duration
          // if it is just removed from vm.files it will stay on first place for 3 seconds before delete
          // bacause of CSS animation duration
          $timeout(function () {
            jQuery('li#media-1').trigger({ type:'mousedown', which:1 });
            vm.files.splice(1, 1);
          });
          // set to second place
          // index = 1;
        } else {
          vm.files.splice(0, 1);
        }
      } else {
        if (index > -1) {
          // remove at index
          vm.files.splice(index, 1);
        }
      }

      // remove from cache if exists
      var indexInCache = -1;
      // get fresh data from local storage if some changes are made
      cachedMedia = localStorageService.get('cachedMedia');
      for (var i = 0; i < cachedMedia.length; i++) {
        if (cachedMedia[i] && file) {
          if (cachedMedia[i].url === file.url) {
            indexInCache = i;
          }
        }
      }

      // console.log('indexInCache', indexInCache);
      if (indexInCache > -1) {
        cachedMedia.splice(indexInCache, 1);
        updateCachedMedia(cachedMedia);
      }
    }

    /**
     * Handle media from the UI
     */
    vm.handleFile = function (file, event) {
      switch(event.which) {
        case 1: // this is left click
        var index = vm.files.indexOf(file);
            vm.changeMainFile(index);
            break;
        case 2: // in case you need some middle click things
            vm.removeFile(file);
            break;
        case 3: // this is right click
            // right click makes problems
            //removeFile();
            break;
        default:
            break;
      }

    }


  }
})();

(function() {
  'use strict';

  angular
    .module('mediaWall')
    .controller('WallController', WallController);

  /** @ngInject */
  function WallController($scope, $state, $stateParams, $timeout, $log, cfpLoadingBar, WallService, ExifService, $sails, $sce, $window, localStorageService) {
    var vm = this;

    // cache with local storage
    var updateCachedMedia = function (updatedCachedMedia) {
      localStorageService.set('cachedMedia', updatedCachedMedia);
    }

    vm.wallMode = 'watch';

    var cachedMedia = localStorageService.get('cachedMedia');
    if (!cachedMedia) {
      cachedMedia = [];
      updateCachedMedia(cachedMedia);
    }

    console.log('paramsi', $stateParams);

    vm.files = [];
    if ($state.current.name === 'wall') {
      if (cachedMedia && cachedMedia.length > 0) {
        vm.files = cachedMedia;
      }
    }
    vm.mainFile = null;

    // UI flags for show/hide
    vm.flags = {};

    vm.isActiveMainImage = false;

    var wallPassword = $stateParams.password || $stateParams.wallPassword || localStorageService.get('wall').password;
    var wallMasterKey = $stateParams.masterKey;

    if ($state.current.name === 'wallVerify' || $state.current.name === 'wallVerifyRemote') {
      vm.isVerifyPage = true;
    }

    // Current authenticated wall
    var wall = WallService.current();

    if (!wall) {
      $state.go('home');
    }

    var id = null;
    if (wall && wall.id) {
      id = wall.id;
    }

    if (!id) {
      return;
    }

    if (wall.image) {
      vm.image = wall.image;
    }

    var roomName = 'wall' + id;

    // Sails.js SOCKET suff --------------------------
    //
    // Unsubscribe on scope destory
    $scope.$on('$destroy', function () {
      $sails.delete("/notify/unsubscribe", { roomName: roomName })
        .then(function (resp) {
          $log.info('Unubscribe OK', resp)
        }, function (resp) {
          $log.error('Unubscribe NOT OK', resp)
        });
      $sails.removeAllListeners();

      if (thisChangedTimeout) {
        $timeout.cancel(thisChangedTimeout);
      }
    });

    var isConnected = false;
    // Subscribe to Sails server for listen for changes
    $sails.on("connect", function () {
      $log.info('Connected to Sails from first try');
      $sails.post("/notify/subscribe", { roomName: roomName })
        .then(function (resp) {
          $log.info('Subscribe OK', resp)
          isConnected = true;
        }, function (resp) {
          $log.error('Subscribe NOT OK', resp)
        });
    });

    // TODO(stepanic): refactor this code and remove redundant code
    $timeout(function () {
      if (!isConnected) {
        $sails.post("/notify/subscribe", { roomName: roomName })
          .then(function (resp) {
            $log.info('Subscribe OK when connected', resp)
            isConnected = true;
          }, function (resp) {
            $log.error('Subscribe NOT OK when connected', resp)
          });
      }
    }, 2000);

    // Listen for changes and update $scope
    var thisChangedTimeout = null;
    // Watching for updates
    $sails.on("notify", function (files) {
      $log.info('Notify', files);
      // Update UI on new data
      updateUI();

      vm.isActiveMainImage = true;

      $timeout(function() {
        vm.isActiveMainImage = false;
      }, 5000);

      if (vm.files[0]) {
        vm.files[0].controls = false;
      }

      for (var i = 0; i < files.length; i++) {
        getOrientation(files[i]);
      }
    });

    // Watching for updates
    $sails.on("notify-remote-approve", function (approvedFile) {
      $log.info('notify-remote-approve', approvedFile);

      // This should not be executed on wallVerifyRemote
      if ($state.current.name === 'wallVerify') {
        vm.files.forEach(function(existingFile) {
          if (existingFile.url === approvedFile.url) {
            existingFile.checked = true;
            vm.verifyFile(existingFile);
            $log.info('Approved file is found and verified', existingFile);
          }
        });
      }
    });
    $sails.on("notify-remote-disapprove", function (disapprovedFile) {
      $log.info('notify-remote-disapprove', disapprovedFile);

      // This should not be executed on wallVerifyRemote
      if ($state.current.name === 'wallVerify') {
        vm.files.forEach(function(existingFile) {
          if (existingFile.url === disapprovedFile.url) {
            vm.unverifyFile(existingFile);
            $log.info('Disapproved file is found and unverified', existingFile);
          }
        });
      }
    });
    // END - Sails.js SOCKET suff --------------------------

    function getOrientation(file) {
      ExifService.getOrientation(file.url, function (orientation) {
        file.orientation = orientation;
        file.trustedUrl = $sce.trustAsResourceUrl(file.url);
        $scope.$apply(function () {
          if ($state.current.name === 'wall') {
            // Keep DESC order
            // Add to start of array
            vm.files.unshift(file);
            vm.mainFile = file;
          } else {
            // Keep ASC order
            vm.files.push(file);
            if (vm.files.length === 1) {
              vm.mainFile = file;
            }
          }

          if ($state.current.name === 'wall') {
            // Main photo + 24 at mosaic
            vm.files = vm.files.slice(0, 25);
          }

          // Skip manual check if server has checked it!
          if ($state.current.name === 'wallVerify') {
            $log.info('wallVerify', file);
            if (file.checked === true) {
              vm.verifyFile(file);
            }
          }
        });
      });
    }

    function updateUI() {
      $scope.windowInnerHeight = $window.innerHeight;
      $scope.windowInnerWidth = $window.innerWidth;
      if ($state.current.name === 'wall') {
        $scope.firstImageWidth = $scope.windowInnerWidth;
        $timeout(function () {
          // Return to 80% of window width after 10 seconds
          $scope.firstImageWidth = $scope.windowInnerWidth * 0.8;
        }, 10000);
      }
    }

    // Update on load
    updateUI();

    // updateUI on window resize
    var w = angular.element($window);
    w.bind('resize', function () {
      $scope.$apply(function () {
        updateUI();
      });
    });

    vm.changeMainFile = function (key) {
      var old = vm.files[0];
      old.controls = false;
      vm.files[0] = vm.files[key];
      if (vm.files[0]) {
        vm.files[0].controls = true;
      }
      if (old) {
        vm.files[key] = old;
      }
    }

    vm.removeFile = function (file) {
      console.log('file to remove', file);
    }

    vm.ToggleWallMode = function() {
      // $state.go('wallVerifiedDirect', { wallPassword: wallPassword });
      $state.go('wallVerified');
    }

    vm.GoToRecord = function() {
      $state.go('record');
    }

    vm.verifyFile = function (file) {

      // Get fresh data if some media is removed to avoid adding removed again
      cachedMedia = localStorageService.get('cachedMedia');
      cachedMedia.unshift(file);
      updateCachedMedia(cachedMedia);

      try {
        // Send to WallVerifiedController
        if (crosstab.supported) {
          crosstab.broadcast('wallNewMedia', file, null);
        } else {
          $log.info('Crosstab is not supported, try over sockets, NOTE: password and masterKey are required');
        }
      } catch (e) {
        $log.error(e);
      }

      // Remote verify
      if ($state.current.name === 'wallVerifyRemote' && wallPassword && wallMasterKey) {
        WallService.approveFile(file, wallPassword, wallMasterKey, function (file, error) {
          if (error) {
            $log.error('File is not approved, error: ', error);
          } else {
            $log.info('File is approved', file);
          }
        });
      }

      // Remove from list
      var index = vm.files.indexOf(file);
      if (index > -1) {
        vm.files.splice(index, 1);
      }

      // REFACTORED(stepanic): this remove first item
      // vm.files.shift();
    }

    vm.unverifyFile = function (file) {
      // Remove from list
      var index = vm.files.indexOf(file);
      if (index > -1) {
        vm.files.splice(index, 1);
      }

      // REFACTORED(stepanic): this remove first item
      // vm.files.shift();

      // Remote unverify
      if ($state.current.name === 'wallVerifyRemote' && wallPassword && wallMasterKey) {
        WallService.disapproveFile(file, wallPassword, wallMasterKey, function (file, error) {
          if (error) {
            $log.error('File is not disapproved, error: ', error);
          } else {
            $log.info('File is disapproved', file);
          }
        });
      }
    }

    /**
     * Handle media from the UI
     */
    vm.handleFile = function (file, event) {
      var index = vm.files.indexOf(file);

      switch(event.which) {
        case 1: // this is left click
            vm.changeMainFile(index);
            break;
        case 2: // in case you need some middle click things
            // Remove from list
            if (index === 0) {
              $scope.$apply(function() {
              vm.files.shift();

              });
              // if (vm.files && vm.files.length > 1) {
                // replace with second
                // vm.changeMainFile(1);
                // set to second place
                // index = 1;
              // }
            } else {
              if (index > -1) {
                // remove at index
                vm.files.splice(index, 1);
              }
            }
            break;
        case 3: // this is right click
            break;
        default:
            break;
      }

    }



  }
})();

(function() {
  'use strict';

  angular
    .module('mediaWall')
    .directive('ngMuted', function(){
      return {
        priority: 99, // it needs to run after the attributes are interpolated
        link: function(scope, element, attr) {

          attr.$observe('ngMuted', function(value) {
            if (value != 'false') {
              element[0].muted = true;
            }
            else{
              element[0].muted = false;
            }
          });
        }
      }
    });

})();




