(function() {
  'use strict';

  angular
    .module('mediaWall')
    .controller('ProfileController', ProfileController);

  /** @ngInject */
  function ProfileController($scope, $state, localStorageService, $timeout, validator) {
    var vm = this;

    vm.email = '';
    vm.displayName = '';

    if (localStorageService.get('currentEmail')) {
      vm.email = localStorageService.get('currentEmail');
    }

    if (localStorageService.get('currentDisplayName')) {
      vm.displayName = localStorageService.get('currentDisplayName');
    }

    vm.saveProfileAndReturnToRecord = function () {

      // Email can be empty
      if (vm.email && !validator.isValidEmail(vm.email)) {
        vm.invalidEmail = true;

        $timeout(function() {
          vm.invalidEmail = false;
        }, 3000);

        return;
      }

      console.log(vm.email, vm.displayName);

      // Store
      localStorageService.set('currentEmail', vm.email ? vm.email : '');
      localStorageService.set('currentDisplayName', vm.displayName ? vm.displayName : '');

      $state.go('record');
    };

  }
})();

