(function() {
  'use strict';

  angular
    .module('mediaWall')
    .controller('HomeController', HomeController);

  /** @ngInject */
  function HomeController($scope, $state, $stateParams, $timeout, $log, cfpLoadingBar, WallService, localStorageService) {
    var vm = this;

    vm.password = '';
    vm.passwordIsFocused = true;

    vm.pin = '';
    vm.pinIsFocused = false;
    var failedPinTry = '';

    $scope.SetValidPin = function (pin) {
      vm.pin = pin;
      vm.wrongPin = false;
      $log.log("PIN: ", pin);
      if (vm.pin.length === 4) {
        if (failedPinTry !== vm.pin) {
          WallService.auth(vm.wall.id, vm.password, vm.pin, function (wall, error) {
            if (error) {
              vm.wrongPin = true;
              failedPinTry = vm.pin;
            } else {
              vm.wall = wall;

              // reset cache before next login
              var cachedMedia = [];
              localStorageService.set('cachedMedia', cachedMedia);

              $state.go('record');
            }
            cfpLoadingBar.complete();
          });
        }
      }
    }

    vm.GoToRecord = function () {
      vm.wrongPassword = false;
      vm.pinIsFocused = false;
      vm.wall = null;
      cfpLoadingBar.start();

      vm.password = vm.password.toLowerCase();

      WallService.info(vm.password, function (wall, error) {
        $log.log(wall, error);
        if (error) {
          vm.wrongPassword = true;
        } else {
          vm.wall = wall;
          if (!wall.hasPin) {

            // reset cache before next login
            var cachedMedia = [];
            localStorageService.set('cachedMedia', cachedMedia);

            $state.go('record');
          } else {
            vm.pinIsFocused = true;
          }
        }
        cfpLoadingBar.complete();
      });
    };

    if ($stateParams.wallPassword) {
      vm.password = $stateParams.wallPassword;
      vm.GoToRecord();
    }

    vm.activeTab = 0;

    vm.OpenTab = function (tabId) {
      if (vm.activeTab === tabId) {
        vm.activeTab = 0;
      } else {
        vm.activeTab = tabId;
      }
    }

  }
})();

