(function() {
  'use strict';

  angular
    .module('mediaWall.helpers')
    .service('validator', validator);

  /** @ngInject */
  function validator() {

    var EMAIL_REGEXP = /^[_a-z0-9]+(\.[_a-z0-9]+)*(\+[a-z0-9-]+)?@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,15})$/;

    function isValidEmail(email) {
      // For empty strings
      if (!email || email.length === 0) {
        return false;
      }
      return EMAIL_REGEXP.test(email);
    }

    return {
      isValidEmail: isValidEmail
    }
  }



})();

